import React from 'react'
import i18n from '../i18n';
import AnchorLink from 'react-anchor-link-smooth-scroll'

export default ({t}) => {
    const changeLanguage = (lng) => {
        i18n.changeLanguage(lng);
      }
    return(
        <div className="header">
            <div className="header_logo"/>
            <div className="header_links">
            <AnchorLink href="#home"><div className="header_link">{t("link1")}</div></AnchorLink>
            <AnchorLink href="#about"> <div className="header_link">{t("link2")}</div></AnchorLink>
            <AnchorLink href="#karta"> <div className="header_link">{t("link3")}</div></AnchorLink>
            <AnchorLink href="#rabota"> <div className="header_link">{t("link4")}</div></AnchorLink>
            <AnchorLink href="#biznes"> <div className="header_link">{t("link5")}</div></AnchorLink>
            </div>
            <div className="lang">
                <div className="lang_button" onClick={() => changeLanguage('ru')}>RU</div>
                <div className="lang_button" onClick={() => changeLanguage('ua')}>UA</div>
            </div>
        </div>
    )
}